@charset "UTF-8";
body{position:relative;}
/*fixed*/

.chapterlist_mod {
	padding: 0;
	position: relative;
	overflow: hidden;
	ul{
		overflow: hidden;
	}
	h2 {
		margin: 0;
		padding:20px 0 15px 15px;
		font-size: 16px;
		color: #666;
		font-weight: 700;border-bottom:solid 1px #e6e6e6;
	}
	li {
		position: relative;
		font-size: 14px;padding:15px 60px 15px 0;margin:0 15px;
		border-bottom:solid 1px #e6e6e6;
		a {
			display: block;
			color: #666;
		}
	}
}

.chapterlist_mod li .chapter-tag{
	position: absolute;
	top:16px;right:0px;
	width:75px;height:20px;line-height:16px;text-align:right;
	span{
		border-radius:3px;font-size:12px;
		display:inline-block;padding:0 2px;
		height:20px;line-height:16px;
		&.free{
			color:#ff4c2f;border:solid 1px #ff4c2f;margin-left:10px;
		}
	}
}
.chapterlist_mod li.on .chapter-tag{
	right:15px;
}
.chapterlist_mod li.on{
	margin:0px;padding:15px 60px 15px 15px;background: #fff;
	a{
		color:#10c1ae;
	}
}

.chapter-cont {
